<template>
    <v-row no-gutters class="pa-2">
        <v-col cols="12" class="py-2 my-2 primary white--text">
            <h1 class="text-center headline white--text font-weight-bold">{{title}}</h1>
        </v-col>
        <v-col cols="12">
            <v-autocomplete
                    v-model="selected_citymun"
                    :items="items"
                    :item-text="item_text"
                    :item-value="item_value"
                    label="Search or select City/Municipality..."
                    v-on:input="onAutocompleteInput"
                    clearable>
            </v-autocomplete>
        </v-col>
        <v-col cols="12">
            <v-row no-gutters class="white--text">
                <v-col class="blue darken-3 py-6">
                    <h2 class="d-block text-center">{{positive}}</h2>
                    <span class="d-block text-center subtitle-2">Positive</span>
                </v-col>
                <v-col class="green darken-2 py-6">
                    <h2 class="d-block text-center">{{recovered}}</h2>
                    <span class="d-block text-center subtitle-2">Recovered</span>
                </v-col>
                <v-col class="red darken-2 py-6">
                    <h2 class="d-block text-center">{{deaths}}</h2>
                    <span class="d-block text-center subtitle-2">Deaths</span>
                </v-col>
            </v-row>
        </v-col>
    </v-row>

</template>

<script>
    import {citymuns} from "../../util/gis"

    export default {
        name: "CaseDashboard",
        props: {
            title: String,
            positive: Number,
            recovered: Number,
            deaths: Number,
            citymunCode: String
        },
        computed: {},
        data() {
            return {
                items: citymuns.data,
                item_text: 'citymunDesc',
                item_value: 'citymunCode',
                selected_citymun: ''
            }
        },
        methods: {
            onAutocompleteInput(n) {
                if (typeof n === 'undefined' || n === '') {
                    this.$emit('case-dashboard-clear')
                } else {
                    this.$emit('case-dashboard-set', n)
                }

            }
        },
        watch: {
            // selected_citymun(n, o) {
            //     if (typeof n === 'undefined' || n === '') {
            //         this.$emit('case-dashboard-clear')
            //     } else {
            //         this.$emit('case-dashboard-set', n)
            //     }
            // },
            citymunCode(n, o) {
                if (n === '000000') {
                    this.selected_citymun = ''
                } else {
                    this.selected_citymun = n
                }
            }
        }
    }
</script>

