<template>
    <l-map id="map"
           :center="center"
           :zoom.sync="zoom"
           :options="{zoomControl: false, preferCanvas: true, doubleClickZoom: false, attributionControl: false}">
        <l-tile-layer v-if="choropleth_loaded" :url="url"></l-tile-layer>
        <l-control position="topleft" class="ma-4">
            <v-menu offset-y>
                <template v-slot:activator="{ on }">
                    <v-btn
                            color="primary"
                            dark
                            fab
                            v-on="on"
                    >
                        <v-icon>mdi-cog</v-icon>
                    </v-btn>
                </template>
                <v-list>
                    <v-list-item
                            v-for="(item, index) in attributes"
                            :key="index"
                            @click="attributeSelected = item"
                    >
                        <v-list-item-title>{{ item.text }}</v-list-item-title>
                    </v-list-item>
                </v-list>
            </v-menu>
        </l-control>
        <l-choropleth-layer v-if="choropleth_loaded" :geojson="geojson" :data="response_data" :value="choroplethValue"
                            titleKey="citymun"
                            geojsonIdKey="citymunCode"
                            idKey="citymunCode"
                            :colorScale="colorScale"
                            min="0"
                            :currentItem="currentItem"
                            stroke-color="424242"
                            :stroke-width="1"
                            v-on:currentItemClick="onCurrentItemClick">
        </l-choropleth-layer>
    </l-map>
</template>

<script>
    import {LMap, LTileLayer, LMarker, LPopup, LGeoJson, LControl, LControlZoom} from "vue2-leaflet"
    import geojson from '../../assets/regionvi_citymun_lite'
    import ChoroplethLayer from './ChoroplethLayer'

    export default {
        name: "Map",
        components: {
            LMap, LTileLayer, LMarker, LPopup, LGeoJson, LControl, LControlZoom,
            'l-choropleth-layer': ChoroplethLayer,
        },
        props: {
            response_data: Array
        },
        created() {
        },
        computed: {
            /*icon() {
                let base = {
                    className: 'custom-icons',
                    prefix: '',
                    glyph: '2'
                }

                return L.icon.glyph({
                    ...base,
                    glyphSize: '24px',
                    glyphAnchor: [0, 0],
                    glyphColor: 'blue',
                    iconUrl: require('../assets/marker.png'),
                    //iconUrl: '',
                    iconSize: [42, 53],
                    iconAnchor: [21, 53],
                    popupAnchor: [0, -64]
                })

            },*/
            geojson() {
                return geojson
            },
            colorScale() {
                switch (this.attributeSelected.value) {
                    case 'covid_positive':
                        return ["#ddd", "#0000FF", "#00009b"]
                    case 'covid_recovered':
                        return ["#ddd", "#00FF00", "#009b00"]
                    case 'covid_deaths':
                        return ["#ddd", "#FF0000", "#9B0000"]
                    case 'pui_unresolved':
                        return ["#ddd", "#FFFF00", "#A7A700"]
                    case 'pui_negative':
                        return ["#ddd", "#FF9700", "#9B5C00"]
                    default:
                        return ["#ffffff", "#999999", "#000000"]
                }
            },
            choroplethValue() {
                switch (this.attributeSelected.value) {
                    case 'covid_positive':
                        return {key: "covid_positive", metric: "CoVid19 Positive"}
                    case 'covid_recovered':
                        return {key: "covid_recovered", metric: "Recovered"}
                    case 'covid_deaths':
                        return {key: "covid_deaths", metric: "Deaths"}
                    case 'pui_unresolved':
                        return {key: "pui_unresolved", metric: "PUIs Unresolved"}
                    case 'pui_negative':
                        return {key: "pui_negative", metric: "PUIs Negative"}
                    default:
                        return {key: "", metric: ""}
                }
            },
            infoTitle() {
                switch (this.attributeSelected.value) {
                    case 'covid_positive':
                        return 'CoViD19 Positive'
                    case 'covid_recovered':
                        return 'Recovered'
                    case 'covid_deaths':
                        return ' Deaths'
                    case 'pui_unresolved':
                        return 'PUIs Unresolved'
                    case 'pui_negative':
                        return 'PUIs Negative'
                    default:
                        return ''
                }
            },
        },
        data() {
            return {
                center: [10.712317, 122.562362],
                zoom: 8,
                url: 'https://api.mapbox.com/styles/v1/dost6ryanb/ck8iat3my0kfv1io1nd2a772a/tiles/512/{z}/{x}/{y}?access_token=pk.eyJ1IjoiZG9zdDZyeWFuYiIsImEiOiI1OGMyZjdjNjZlYjlhNTMyNDc0NGQxOTY4ZDJlZjIxNyJ9.dkASVYIEPInwAEkwUkaGhQ',
                attributes: [
                    {text: 'COVID-19 Positive', value: 'covid_positive'},
                    {text: 'Recovered', value: 'covid_recovered'},
                    {text: 'Deaths', value: 'covid_deaths'},
                    {text: 'PUIs Negative', value: 'pui_negative'},
                    {text: 'PUIs Unresolved', value: 'pui_unresolved'},
                ],
                attributeSelected: {text: 'COVID-19 Positive', value: 'covid_positive'},
                choropleth_loaded: true,
                summary_loaded: false,
                currentItem: {name: "", value: 0, key: ""},
                response_total: {},
            }
        },
        methods: {
            getMax(i) {
                if (i === 0) {
                    return 2
                } else {
                    return i & 1 ? i + 1 : i
                }
            },
            onCurrentItemClick(e) {
                if (e.key !== "") {
                    this.$emit('choropleth-set', e.key)
                } else {
                    this.$emit('choropleth-clear')
                }
            },
        },
        watch: {
            zoom(n, o) {
                console.log(`watch zoom ${o} to ${n}`)
            },
            attributeSelected(n, o) {
                this.choropleth_loaded = false
                this.$nextTick(() => {
                    this.choropleth_loaded = true
                })
            },
            watch: {}
        }
    }
</script>

<style scoped>
    #map {
        z-index: 1;

    }
</style>